import {oneOf} from "../utils/utils";
export default {
  name: 'myRadio',
  props: {
    selValue: {
      default: '',
      type: [Boolean, String]
    },
    label: {
      default: '',
      type: [Boolean, String]
    },
    group: {
      default: false,
      type: Boolean
    },
    disabled: {
      default: false,
      type: Boolean
    },
    border: {
      default: false,
      type: Boolean
    },
    button: {
      default: false,
      type: Boolean
    },
    textColor: {
      default: '',
      type: String
    },
    fill: {
      default: '',
      type: String
    },
    options: {
      default: function () {
        return [];
      },
      type: Array
    },
    size: {
      defalult: 'medium',
      validate: function (val) {
        return oneOf(val, ['large', 'medium', 'small', 'mini']);
      }
    }
  },
  computed: {
    selectModel(){
      this.value = this.selValue;
    }
  },
  data() {
    return {
      value: ''
    }
  },
  methods: {
    handleChange(data) {
      this.$emit('change', data);
    }
  }
}
